import axios from 'axios';
import { writable } from "svelte/store";

//1. 프로미스
const setPromise = (url) => { 
    const options = {
        method: 'GET',
        url: url,
        params: {language: 'ko', page: '1'},
        headers: {
            accept: 'application/json',
            Authorization: 'Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiI5MmRjOWUwMGU1NTg1MGFjMGZhOGVjOTBkZjAwNTVhMyIsInN1YiI6IjY2NWQ1MGQxZTg1NTMzZTgzYmRkODZhZiIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.0rtEfzUVfo3KkHnlzudZt1-58VoJAwUjXLfSwn9nIK0'
        }
    };
    const getPromise = async () => {
        try {
            const res = await axios.request(options);
            return res;
        }catch(error) {
            throw new Error(error);
        }
    };

    const { subscribe } = writable(getPromise());
    return {
        subscribe,
    }
}

